@import url(https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/Recipekorea.woff);
body {
  background-color: linen;
  margin: 0;
  font-family: "Recipekorea", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}
* {
  text-align: center;
}
.homeimg {
  margin-top: 20px;
  width: 150px;
  height: auto;
  cursor: pointer;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}
.homeimg:hover {
  -webkit-transform: rotate(10deg);
          transform: rotate(10deg);
}
h1 {
  margin-top: 100px;
  margin-bottom: 100px;
  font-style: oblique;
  font-size: 70px;
}
span.complete {
  font-size: 80px;
  font-style: normal;
  color: mediumblue;
  cursor: help;
}
span.computer {
  font-size: 80px;
  font-style: normal;
  color: dimgrey;
  cursor: help;
}
h3 {
  margin-top: 50px;
  font: message-box;
  font-weight: bold;
  font-size: 40px;
}
.des {
  margin-top: 50px;
  font: sans-serif;
  font-size: 20px;
  font-weight: 600;
  color: darkgrey;
}

p {
  font-size: 40px;
}
input {
  font-size: 20px;
}
.btn_submit {
  text-align: center;
  font-size: 15px;
  font-family: 고딕체;
  font-weight: 800;
  color: #ffffff;
  padding: 6px 12px;
  background: black;
  background-color: #428bca;
  border: #357ebd 1px solid;
  border-radius: 0px;
  cursor: pointer;
}
.btn_submit:hover {
  visibility: visible;
  opacity: 0.6;
}
.reward {
  visibility: hidden;
  width: 200px;
  height: auto;
}

.prob_head {
  text-align: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  border-bottom: 1px solid black;
  font: "Recipekorea";
}
span {
  color: orangered;
}
.reward_img {
  width: 300px;
  height: auto;
}

h1 {
  margin-bottom: 80px;
}
.homebtn {
  margin-top: 30px;
  font-size: 30px;
  text-align: center;
  border: none;
  cursor: pointer;
  background-color: white;
  transition: background-color 1.5s;
}
.homebtn:hover {
  background-color: black;
  color: white;
}
.againbtn {
  font-size: 30px;
  text-align: center;
  border: none;
  cursor: pointer;
  background-color: white;
  letter-spacing: 20px;
  width: 300px;
  height: 150px;
  transition: letter-spacing 1s, background-color 1s;
}
.againbtn:hover {
  letter-spacing: normal;
  background-color: black;
  color: white;
}
.result_img {
  width: 500px;
  height: auto;
  border: none;
}

